import  React from 'react';
import {Card} from 'antd';
import {Badge} from 'react-bootstrap';
import { WiDaySunny,WiCloudDown, WiCelsius } from 'weather-icons-react';



class Weather extends React.Component{

    render(){
        return(
            <div>
            <div>
                <h3>
                        <Card
                            style={{ marginTop: 5 }}
                            type="inner"
                            headStyle = {{backgroundColor: '#333'}}
                            title={ <h3><Badge variant="secondary">Weather Information</Badge></h3>}
                        
                        >
                            <div style={{textAlign:"left",width:"100%"}}>
                                < WiDaySunny size={35}/> 
                            </div>
                            <div style={{textAlign:"left",width:"100%"}}>
                                <WiCloudDown size={35}/>
                            </div>
                            <div style={{textAlign:"left",width:"100%"}}>
                                <WiCelsius size={35}/>
                            </div>
                        </Card>
                      
                </h3>
               
               
          </div>
            </div>
        )
    }
}

export default Weather; 